import React, { useState, useEffect } from 'react'
import { View, Text, Image, Button, Input } from '@tarojs/components'
import { AtIcon, AtModal, AtModalHeader, AtModalContent, AtModalAction } from 'taro-ui'
import Taro, { useDidShow } from '@tarojs/taro'
import { walletService } from '@/services/wallet.service'
import {
  getCurrentUserInfo,
  maskPhone,
  checkAndAutoLogin,
  wechatLogin,
  bindPhone,
  WechatLoginResult,
  UserInfo
} from '@/utils/user'
import LogoImg from '@/assets/icons/logo.png'
import './index.scss'

const Mine: React.FC = () => {
  const [balance, setBalance] = useState(0)
  const [userInfo, setUserInfo] = useState<UserInfo | null>(null)
  const [loading, setLoading] = useState(false)

  // 手机号绑定弹窗状态
  const [showBindPhoneModal, setShowBindPhoneModal] = useState(false)
  const [phoneInput, setPhoneInput] = useState('')
  const [bindingPhone, setBingingPhone] = useState(false)
  const [currentOpenid, setCurrentOpenid] = useState('')

  useEffect(() => {
    initUser()
  }, [])

  // 页面显示时刷新数据
  useDidShow(() => {
    refreshUserData()
  })

  // 初始化用户信息
  const initUser = async () => {
    setLoading(true)
    try {
      // 先尝试从本地获取用户信息
      const localUser = getCurrentUserInfo()
      if (localUser && localUser.phone) {
        setUserInfo(localUser)
        fetchBalance()
        // 后台静默尝试刷新用户信息
        checkAndAutoLogin().then(freshUser => {
          if (freshUser) {
            setUserInfo(freshUser)
          }
        })
      } else {
        // 没有本地用户，尝试自动登录
        const userInfo = await checkAndAutoLogin()
        if (userInfo) {
          setUserInfo(userInfo)
          fetchBalance()
        } else {
          // 需要手动触发登录
          console.log('需要用户手动登录')
          // 确保userInfo为null，显示登录按钮
          setUserInfo(null)
        }
      }
    } catch (error) {
      console.error('初始化用户失败:', error)
      // 失败时也要确保显示登录按钮
      setUserInfo(null)
    } finally {
      setLoading(false)
    }
  }

  // 刷新用户数据
  const refreshUserData = async () => {
    if (userInfo?.phone) {
      fetchBalance()
    }
  }

  const fetchBalance = async () => {
    try {
      const currentBalance = await walletService.getBalance()
      setBalance(currentBalance)
    } catch (error) {
      console.error('获取余额失败:', error)
    }
  }

  const menuItems = [
    {
      icon: 'file-generic',
      title: '我的订单',
      path: '/pages/order/list/index',
      arrow: true
    },
    // {
    //   icon: 'money',
    //   title: '我的券包',
    //   path: '/pages/mine/coupons/index',
    //   badge: '3',
    //   arrow: true
    // },
    // {
    //   icon: 'gift',
    //   title: '邀请有奖',
    //   path: '/pages/mine/invite/index',
    //   arrow: true
    // },
    // {
    //   icon: 'phone',
    //   title: '联系客服',
    //   path: '/pages/mine/contact/index',
    //   arrow: true
    // },
    {
      icon: 'help',
      title: '关于我们',
      path: '/pages/mine/about/index',
      arrow: true
    }
  ]

  const handleMenuClick = (item: typeof menuItems[0]) => {
    Taro.navigateTo({ url: item.path })
  }

  // 处理微信登录
  const handleLogin = async () => {
    if (loading) return

    setLoading(true)
    try {
      const loginResult = await wechatLogin()

      if (loginResult.needBindPhone) {
        // 需要绑定手机号
        setCurrentOpenid(loginResult.openid)
        setShowBindPhoneModal(true)
      } else if (loginResult.userInfo) {
        // 登录成功
        setUserInfo(loginResult.userInfo)
        fetchBalance()
        Taro.showToast({
          title: '登录成功',
          icon: 'success'
        })
      }
    } catch (error) {
      console.error('微信登录失败:', error)
      Taro.showToast({
        title: '登录失败，请重试',
        icon: 'error'
      })
    } finally {
      setLoading(false)
    }
  }

  // 处理手机号绑定
  const handleBindPhone = async () => {
    if (!phoneInput.trim()) {
      Taro.showToast({
        title: '请输入手机号',
        icon: 'error'
      })
      return
    }

    // 简单的手机号验证
    const phoneRegex = /^1[3-9]\d{9}$/
    if (!phoneRegex.test(phoneInput.trim())) {
      Taro.showToast({
        title: '手机号格式不正确',
        icon: 'error'
      })
      return
    }

    setBingingPhone(true)
    try {
      await bindPhone(currentOpenid, phoneInput.trim())

      // 绑定成功，获取最新用户信息
      const latestUserInfo = getCurrentUserInfo()
      if (latestUserInfo) {
        setUserInfo(latestUserInfo)
        fetchBalance()
      }

      setShowBindPhoneModal(false)
      setPhoneInput('')

      Taro.showToast({
        title: '绑定成功',
        icon: 'success'
      })
    } catch (error) {
      console.error('手机号绑定失败:', error)
      Taro.showToast({
        title: '绑定失败，请重试',
        icon: 'error'
      })
    } finally {
      setBingingPhone(false)
    }
  }

  // 取消手机号绑定
  const handleCancelBindPhone = () => {
    setShowBindPhoneModal(false)
    setPhoneInput('')
    setCurrentOpenid('')
  }

  const handleBalanceClick = () => {
    Taro.navigateTo({
      url: '/pages/mine/balance/index'
    })
  }

  return (
    <View className="mine-page">
      <View className="header-section">
        <View className="user-card">
          <View className="user-info">
            <Image
              className="avatar"
              src={userInfo?.avatar || LogoImg}
              mode="aspectFill"
            />
            {userInfo ? (
              <Text className="phone">
                {userInfo.nickname || userInfo.username || maskPhone(userInfo.phone)}
              </Text>
            ) : (
              <Button
                className="login-btn"
                onClick={handleLogin}
                loading={loading}
                disabled={loading}
                size="mini"
                type="primary"
              >
                {loading ? '登录中...' : '微信登录'}
              </Button>
            )}
          </View>
          {userInfo && (
            <View className="balance-info" onClick={handleBalanceClick}>
              <Text className="balance-label">余额: </Text>
              <Text className="balance-amount">¥ {balance.toFixed(2)}</Text>
              <AtIcon value="chevron-right" size="16" color="#fff" />
            </View>
          )}
        </View>
      </View>

      <View className="menu-section">
        {menuItems.map((item, index) => (
          <View 
            key={index}
            className="menu-item" 
            onClick={() => handleMenuClick(item)}
          >
            <View className="menu-left">
              <AtIcon 
                prefixClass="icon" 
                value={item.icon} 
                size="20" 
                color="#666"
              />
              <Text className="menu-title">{item.title}</Text>
            </View>
            <View className="menu-right">
              {item.badge && (
                <View className="badge">{item.badge}</View>
              )}
              {item.arrow && (
                <AtIcon value="chevron-right" size="16" color="#999" />
              )}
            </View>
          </View>
        ))}
      </View>

      {/* 手机号绑定弹窗 */}
      <AtModal
        isOpened={showBindPhoneModal}
        onCancel={handleCancelBindPhone}
        onConfirm={handleBindPhone}
      >
        <AtModalHeader>绑定手机号</AtModalHeader>
        <AtModalContent>
          <View className="bind-phone-content">
            <Text className="bind-phone-tips">
              请输入您的手机号，用于账号登录和信息接收
            </Text>
            <Input
              className="phone-input"
              type="number"
              placeholder="请输入手机号"
              value={phoneInput}
              onInput={(e) => setPhoneInput(e.detail.value)}
              maxlength={11}
              disabled={bindingPhone}
            />
          </View>
        </AtModalContent>
        <AtModalAction>
          <Button onClick={handleCancelBindPhone}>取消</Button>
          <Button onClick={handleBindPhone} disabled={bindingPhone}>
            {bindingPhone ? '绑定中...' : '确认绑定'}
          </Button>
        </AtModalAction>
      </AtModal>
    </View>
  )
}

export default Mine